export default {
	data() {
		return {
			sList:[],
			keywords:"",   //搜索的内容
			page:1 ,//当前第几页
			totalpage:1 //总页数
		}
	},
	onLoad(){
		uni.showLoading({
			mask:true,
			title:"请等一下...."
		});
		uni.showNavigationBarLoading();
		uni.request({
			url:"http://t.yushu.im/v2/movie/top250",
			method:"GET",
			success: (res)=> {
				console.log(res.data.subjects);
				var a = res.data.subjects;
				this.sList = a;
			},
			complete:() =>{
				uni.hideNavigationBarLoading();
				uni.hideLoading();
			}
			
		})
	},
	onReachBottom(){
		var page = this.page;   
		var keywords = this.keywords;
		var totalpage = this.totalpage;
		
		if( page > totalpage){
			return ;
		}
		
		this.pagedTrailerList(keywords,page,5);
	},
	methods: {
		showinfo(e){
			var id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url:"../info/info?id="+id
			})
		},
		pagedTrailerList(keywords,page,totalpage){
			uni.showLoading({
				mask:true,
				title:"请等一下...."
			});
			uni.showNavigationBarLoading();
			uni.request({
				url:'http://t.yushu.im/v2/movie/top250?start='+page+'&count='+totalpage,
				method:"GET",
				success: (res)=> {
					console.log(res.data.subjects);
					var a = res.data.subjects;
					this.sList = this.sList.concat(a);
					this.totalpage  = res.data.total;
					this.page = page;
				},
				complete:() =>{
					uni.hideNavigationBarLoading();
					uni.hideLoading();
				}
				
			})
		},
		searchshow(e){
			var value = e.detail.title;
			console.log(value);
			this.keywords = value;
			this.sList = [];
			this.pagedTrailerList(value,1,15);
		}
	}
}
